@charset "UTF-8";
.main-nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--white);
  box-shadow: var(--box-shadow);
  transition: var(--transition-200); }
  .main-nav .menu-btn {
    display: none; }
  .main-nav .nav-menu {
    height: 100%;
    display: flex;
    align-items: center; }
  .main-nav .nav-item {
    position: relative;
    overflow: hidden;
    height: 100%;
    padding: 0 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--dark);
    font-weight: bold;
    text-decoration: none;
    opacity: .75;
    transition: var(--transition-200); }
    .main-nav .nav-item--logo {
      display: flex;
      justify-content: center;
      align-items: center;
      padding-left: 15px;
      padding-right: 15px;
      opacity: 1; }
      .main-nav .nav-item--logo .nile-typo {
        height: 32px;
        filter: invert(100%);
        transition: var(--transition-200); }
      .main-nav .nav-item--logo .logo-wrapper {
        position: relative;
        height: 40px;
        width: 40px;
        margin-right: 20px;
        border-radius: 100px;
        filter: invert(100%);
        transition: var(--transition-200); }
        .main-nav .nav-item--logo .logo-wrapper img.logo {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          transition: var(--transition-200); }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-1  {
            opacity: 1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-2  {
            opacity: .1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-3  {
            opacity: .1 !important; }
          .main-nav .nav-item--logo .logo-wrapper img.logo.logo-rotation-4  {
            opacity: .1 !important; }
    .main-nav .nav-item:before {
      content: '';
      position: absolute;
      bottom: -5px;
      height: 10px;
      width: 0;
      border-radius: var(--border-radius);
      transition: var(--transition-200); }
    .main-nav .nav-item:hover, .main-nav .nav-item.active {
      opacity: 1;
      background-color: var(--black_5);
      color: var(--primary); }
      .main-nav .nav-item:hover:before, .main-nav .nav-item.active:before {
        content: '';
        position: absolute;
        bottom: -5px;
        height: 10px;
        width: 50px;
        border-radius: var(--border-radius);
        background-color: var(--primary); }
    .main-nav .nav-item:hover {
      cursor: pointer; }
      .main-nav .nav-item:hover:before {
        width: 10px; }
  .main-nav.top {
    background-color: var(--black_10);
    box-shadow: none; }
    .main-nav.top .nav-item {
      color: var(--white); }
      .main-nav.top .nav-item--logo .logo-wrapper {
        filter: invert(0%); }
      .main-nav.top .nav-item--logo .nile-typo {
        filter: invert(0%); }
      .main-nav.top .nav-item:hover, .main-nav.top .nav-item.active {
        background-color: var(--white_10); }

img.logo.logo-rotation-2 {
  opacity: .1; }

img.logo.logo-rotation-3 {
  opacity: .1; }

img.logo.logo-rotation-4 {
  opacity: .1; }

@media only screen and (max-width: 1100px) {
  .main-nav .nav-menu .nav-item {
    padding: 0 40px; } }

@media only screen and (max-width: 992px) {
  .main-nav.open {
    background-color: var(--dark); }
    .main-nav.open.normal {
      background-color: var(--white); }
  .main-nav .menu-btn-container {
    height: 70px;
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: var(--transition-300); }
    .main-nav .menu-btn-container .menu-btn {
      position: relative;
      height: 29px;
      width: 29px;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: var(--transition-300_cubic); }
      .main-nav .menu-btn-container .menu-btn span {
        position: absolute;
        height: 3px;
        width: 100%;
        background-color: var(--white);
        border-radius: var(--border-radius-sm);
        transition: var(--transition-300_cubic); }
      .main-nav .menu-btn-container .menu-btn .top {
        top: 5px; }
      .main-nav .menu-btn-container .menu-btn .bottom {
        bottom: 5px; }
    .main-nav .menu-btn-container.open {
      background-color: var(--white_10); }
      .main-nav .menu-btn-container.open .menu-btn {
        transform: rotate(180deg); }
      .main-nav .menu-btn-container.open .center {
        width: 3px; }
      .main-nav .menu-btn-container.open .top {
        transform: rotate(45deg);
        top: 13px; }
      .main-nav .menu-btn-container.open .bottom {
        transform: rotate(-45deg);
        bottom: 13px; }
    .main-nav .menu-btn-container:hover {
      cursor: pointer;
      background-color: var(--white_10); }
      .main-nav .menu-btn-container:hover .center {
        width: 15px; }
      .main-nav .menu-btn-container:hover .top, .main-nav .menu-btn-container:hover .bottom {
        width: 25px; }
    .main-nav .menu-btn-container.open:hover .center {
      width: 3px; }
    .main-nav .menu-btn-container.normal span {
      background-color: var(--dark); }
    .main-nav .menu-btn-container.normal:hover {
      background-color: var(--black_10); }
  .main-nav .nav-menu {
    display: none; }
    .main-nav .nav-menu .nav-item {
      height: 100px;
      border-bottom: var(--border-sm_light); }
      .main-nav .nav-menu .nav-item:first-child {
        border-top: var(--border-sm_light); }
      .main-nav .nav-menu .nav-item:before {
        left: -5px;
        bottom: auto;
        width: 10px;
        height: 50px; }
    .main-nav .nav-menu.open {
      position: fixed;
      top: 70px;
      left: 0;
      width: 100%;
      height: calc(100vh - 70px);
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: stretch;
      background-color: var(--dark); }
    .main-nav .nav-menu.normal {
      background-color: var(--white); }
      .main-nav .nav-menu.normal .nav-item {
        border-bottom: var(--border-sm_dark); }
        .main-nav .nav-menu.normal .nav-item:first-child {
          border-top: var(--border-sm_dark); } }
